import React, { useState, useEffect } from "react";
import "../componets/log-in/login.css";

const NotFoundPage: React.FC = () => {
  const [show404, setShow404] = useState(false);

  useEffect(() => {
    const t = setTimeout(() => setShow404(true), 10000);
    return () => clearTimeout(t);
  }, []);

  return (
    <div
      className="auth-shell"
      style={{
        display: "block",
        width: "100vw",
        height: "100vh",
        position: "relative",
        overflow: "hidden",
      }}
    >
      <aside
        className="brand-side"
        style={{
          width: "100vw",
          height: "100vh",
          position: "relative",
        }}
      >
        <div className="brand-sheen" />
        <div className="brand-pattern" />

        {/* Centered Content */}
        <div
          style={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
            textAlign: "center",
          }}
        >
          {!show404 && (
            <>
              <div
                className="spinner"
                style={{
                  width: "70px",
                  height: "70px",
                  border: "6px solid rgba(255,255,0,0.3)",
                  borderTopColor: "var(--gold-2)",
                  borderRadius: "50%",
                  animation: "spin 1s linear infinite",
                  margin: "0 auto 20px auto",
                }}
              />

              <p
                style={{
                  color: "var(--off-white)",
                  fontSize: "1.6rem",
                  marginTop: "10px",
                }}
              >
                Đang tải trang…
              </p>
            </>
          )}

          {show404 && (
            <>
              <h1
                style={{
                  color: "var(--off-white)",
                  fontSize: "8rem",
                  fontWeight: 700,
                  margin: 0,
                }}
              >
                404
              </h1>
              <p
                style={{
                  color: "var(--light-gray)",
                  fontSize: "1.6rem",
                  marginTop: "10px",
                }}
              >
                Không tìm thấy trang bạn yêu cầu.
              </p>
            </>
          )}
        </div>
      </aside>

      {/* Spinner animation + kill right column */}
      <style>
        {`
        @keyframes spin {
          from { transform: rotate(0deg); }
          to { transform: rotate(360deg); }
        }

        .auth-shell .form-side {
          display: none !important;
        }
      `}
      </style>
    </div>
  );
};

export default NotFoundPage;

export const Head = () => <title>Not found</title>;
